.header{
  background: white;
  text-align: center;
  display: flex;
  justify-content: space-between;
  padding:10px 30px;
}

.menu-button{
    display: none;
  }
.info-contact ul{
    display: inline-block;
    list-style: none;

    text-align: left;
  }
.nav-bar{
  display: none;
}
.nav-bar nav{
  background: #33BEFF;
  position: relative;
  width: 100%;
transition: transform 0.8s;
}
.nav-bar nav ul{
  list-style: none;
   height: 40px;
    display: flex;
    justify-content: space-between;
    padding: 0px 30px;
}
.nav-bar nav ul li{
  height: 40px;
}

.nav-bar-b nav{
  background: #33BEFF;
  position: relative;
  width: 100%;
transition: transform 0.8s;
}
.nav-bar-b nav ul{
  list-style: none;
   height: 40px;
    display: flex;
    justify-content: space-between;
    padding: 0px 30px;
}
.nav-bar-b nav ul li{
  height: 40px;
}
.titulo{
  display: block;
}

.header-image{
  height: 250px;
}
#cb-toggle{
  display: none;
}
#cb-toggle:checked + nav {
  transform: translate(100%);
}


@media (max-width: 768px) {
  .titulo{
    display: none;
  }
  .info-contact ul{
    list-style: none;
    display: none;
  }
 .menu-button{
    display: inline-block;
  } 
  .nav-bar{
    display: block;
  } 
  .nav-bar nav{
    position: absolute;
  }
  .nav-bar nav:target{
    transform: translate(100%);
  }
 
  .nav-bar nav ul{
    display: block;
    height: auto;
  }
  .nav-bar nav ul li{
    height: 40px;
    width: 100%;
  }
  .nav-bar-b{
    display: none;
  }

}

.page-enter {
    transform: translateX(-100%);
}
.page-enter-active {
    opacity: 1;
    transform: translateX(0%);
    transition: all 300ms ease-in;
}
.page-exit {
    opacity: 1;
    transform: translateX(0%);
}
.page-exit-active {
    transform: translateX(-100%);
    transition: all 300ms ease-out;
}
